.task-container{
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-bottom: 0.5em;
    width:90%;
}


.task-title{
    flex: 3;
    margin: 0;
    font-size: 1.2em;
    text-align: left;
    border: 1px solid #ccc;
    padding: .4em;
}

.task-container-btn{
    display: flex;
    flex: 1;
}

.task-btn {
  padding: 0.4em;
  border: 1px solid teal ;
  margin-left: .5em;
  color: white;
  background-color: teal;
  font-size: large;
}

@media(max-width: 435px){
.task-container{
    padding: 0;

    }
.task-title{
    font-size: .6em;
     width: 50%;
    word-wrap: break-word;
    text-overflow: ellipsis;
    
}
.task-btn{
    padding: 0;
    font-size: medium;
}
}