.App {
  text-align: center;
  margin: 0;
}
.task-title{
  text-align: center;
}

.tasks-container{
  width: 75%;
  margin: 1.3em  auto;
  text-align: center;
}

.edit-input{
  width: 70%;
  padding: 0.5em;
  font-size: 1em;
  border: 1px solid white;
  border-radius: 4px;
}

.btn-container{
  margin-top:  2em;
  margin-left: 50%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.btn-container button {
  margin-right: 0.8em;
  color: white;
  padding: .5em;
  font-size: 1.1em;
  border-radius: 4px;
  font-weight: 600;
}

.btn-edit{
  background-color: rgb(47, 145, 152);
  border: 1px solid rgb(47, 145, 152);
  
}

.btn-edit:hover{
  background-color:rgb(32, 103, 108);
  border: 1px solid rgb(32, 103, 108);
  
}


.btn-cancel{
  background-color: tomato;
  border: 1px solid tomato;
}

.btn-cancel:hover{
  background-color: red;
  border: 1px solid red;
}

@media(max-width:435px){
  .tasks-container{
    width: 70%;
    margin: 1.3em  0;
  }
}