.input-container {
  width: 75%;
  margin: 0 auto;
  display: flex;
}

.task-input {
  width: 80%;
  font-size: 1.4em;
  border-radius: 2px;
  padding: 0.2em;
  border: 1px solid #ccc;
}

.task-input:focus {
  outline: #ccc;
}

.btn-primary {
  padding: 0.5em;
  font-size: 1em;
  border: 1px solid steelblue;
  background-color: steelblue;
  color: wheat;
  border-radius: 2px;
  margin-left: 0.5em;
}

@media (max-width: 435px) {
  .input-container {
    width: 90%;
  }
  .task-input {
    width: 70%;
    font-size: 0.8em;
  }
  .btn-primary {
    font-size: 0.8em;
  }
}
